#footer {
   z-index: 1;
   @include position(relative);
   background-color: $section-alt-darkblue;

   .container {
      .footer-container-flex {
         @include display(flex);
         @include flex-wrap(wrap);
         @include justify-content(space-between);
         padding: 0px;

         .f-item {
            @include flex(0 0 49%);
            margin-right: 0px;

            .sub-items {
               list-style-type: none;
               padding: 0px;
               text-align: center;

               .sub-item {
                  @include position(relative);
                  margin-left: 15px;
                  color: $section-alt;
                  @include display(block);
                  margin-right: 15px;

                  a {
                     @include display(inline-block);
                     color: $section-alt;
                     border-bottom: 1px dotted transparent;
                     line-height: 1.1;
                     margin-bottom: .2rem;

                     &:hover {
                        border-bottom-color: $section-alt; } } } } }

         .fb-comment {
            @include flex(0 0 100%);
            margin-bottom: 2rem;
            height: 100%;

            .fb-comment-container {
               height: 280px;
               overflow-y: auto;
               border-radius: 2px;
               background-color: white;

               .fb-comments, .fb-comments * {
                  width: 100% !important; } } } } } }

#copyright {
   background-color: $section-alt;

   .copyright-container {
      padding: .6rem 0px;
      text-align: center;

      a {
         @extend .smaller;

         &:hover {
            border-bottom: 1px dotted $section-alt-darkblue; } }

      p {
         margin: 0px; } } }

@media screen and (min-width: 470px) {
   #footer {
      .container {
         .footer-container-flex {
            .f-item {
               .sub-items {
                  .sub-item {
                     @include display(inline-block);
                     width: 100%; } } } } } } }

@media screen and (min-width: 768px) {
   #footer {
      .container {
         .footer-container-flex {
            .f-item {
               &:last-of-type {
                  margin-right: 0px;

                  span {
                     padding-left: 0px; } }

               .sub-items {
                  text-align: left;

                  .sub-item {
                     @include display(block);

                     &:before {
                        content: "";
                        @include display(block);
                        @include size(5px);
                        border: 1px solid $section-alt;
                        border-radius: 50%;
                        @include position(absolute, 11px null null -14px); } } } } } } } }


@media screen and (min-width: 992px) {
   #footer {
      .container {
         .footer-container-flex {
            @include flex-wrap(nowrap);

            .f-item {
               @include flex(1 0 auto);
               margin-right: 3rem; }

            .fb-comment {
               @include flex(0 0 520px);
               margin-bottom: 0px;

               .fb-comment-container {
                  height: 170px; } } } } } }


