.page {
   @extend .bg-slant;

   .breadcrumb {
      z-index: 1;
      @include size(100% auto);
      @include position(relative);
      background-color: $section-main;
      border: {
         width: 1px 0px;
         style: solid;
         color: $section-alt-blue; }

      .breadcrumb-container {
         padding: .15rem .5rem .1rem;

         ul {
            list-style-type: none;
            padding: 0px;
            margin: 0px;

            span {
               @include display(none); }

            .home {
               ~ * {
                  padding-left: 7px;

                  &:before {
                     content: "";
                     color: $p-color;
                     font-weight: 300;
                     @include position(absolute, null null null -.2rem); } } }

            li {
               @include position(relative);
               @include display(inline-block);
               padding: 0px .2rem;

               &.active {
                  a {
                     color: $section-alt-darkblue; } }

               a {
                  font-size: .9rem;
                  border-bottom: 1px dotted transparent;

                  &:hover {
                     border-color: $section-alt-darkblue; } } } } } } }


@media screen and (min-width: 500px) {
   .page .breadcrumb .breadcrumb-container {
      padding: .4rem .5rem .3rem; } }

@media screen and (min-width: 768px) {
   .page {
      .breadcrumb {
         @include size(100% auto);
         padding-top: 52px;
         border: 0px;
         background-color: initial;

         &:before {
            content: "";
            @include position(absolute, 0px null null 0px);
            @include size(50% 90%);
            background-color: $section-alt; }

         .breadcrumb-container {
            overflow: visible;

            &:before {
               content: "";
               z-index: -2;
               border-top: 39px solid $section-alt-blue;
               border-right: 34px solid transparent;
               @include position(absolute, 0px -33px 0px -100%); }

            &:after {
               content: "";
               z-index: -1;
               border-top: 38px solid $section-alt;
               border-right: 33px solid transparent;
               @include position(absolute, 0px -32px 0px -100%); }

            ul {

               span {
                  @include display(inline);
                  font: {
                     size: .9rem;
                     weight: 300; } }

               .home {
                  margin-left: .2rem;

                  ~ * {
                     padding-left: 12px;
                     &:before {
                        content: "";
                        color: $p-color;
                        font-weight: 300;
                        @include position(absolute, null null null -.2rem); } } }

               li {
                  padding: 0px .5rem; } } } } } }
